.footer {
    @apply bg-[#2A3266] text-white;

    .below {
        @apply relative flex justify-between max-w-[1132px] mx-auto mt-8 mobile:mt-11 pb-10 px-4 z-10;
    }

    .contacts {
        @apply mt-auto max-w-[494px] mobile:max-w-none mobile:mx-auto mobile:mb-7 mobile:mt-4 flex mobile:block whitespace-nowrap;
    }

    .title {
        @apply text-xl;
    }
}

.contacts {
    li {
        @apply flex w-[300px] mobile:w-[320px] first:max-w-[148px] bg-white rounded-lg p-2 text-[#1E282D] last:rounded-tr-none mobile:last:rounded-tr-lg mobile:first:max-w-none;

        & + li {
            @apply ml-4 mobile:ml-0 mobile:mt-4;
        }
    }

    svg {
        @apply min-w-[24px] -mt-[2px] block;
    }

    h4 {
        @apply font-semibold text-sm;
    }

    a {
        @apply text-xs font-medium;

        & + a {
            @apply mobile:block before:content-['|'] before:text-[#0F85FF] before:px-2 mobile:before:content-none;
        }
    }
}

.contacts_unstyled {
    @apply pl-9;
    li {
        @apply flex font-medium;

        & + li {
            @apply mt-10;
        }

        h4 {
            @apply mt-[2px] font-semibold mb-4;
        }

        a {
            @apply inline-block;
        }
    }
}

.author {
    @apply text-[0] flex justify-between mt-auto;

    a {
        @apply opacity-50 transition-opacity duration-300 hover:opacity-100;
    }

    .line {
        @apply w-[2px] h-5 bg-[rgba(255,255,255,0.5)] rounded-full mx-3;
    }
}

.docsHorizontal {
    @apply flex flex-wrap mt-auto mobile:flex-col mobile:w-1/2 mobile:mt-16 mobile:ml-auto;

    li {
        @apply pt-3 mobile:pt-2 first:pt-0;

        &:nth-child(2) {
            @apply pt-0 mobile:pt-2;
        }
        &:nth-child(2n-1) {
            @apply w-[40%] mobile:w-full;
        }
        &:nth-child(2n) {
            @apply w-[60%] mobile:w-full text-right mobile:text-left border-l mobile:border-l-0 border-l-white;
        }
    }

    a {
        @apply text-xs mobile:text-[10px] hover:font-medium;
    }
}

.docs {
    @apply min-w-[182px] text-[12px] border-l mobile:border-l-0 mobile:order-last text-right mobile:text-base mobile:text-left mobile:border-t mobile:pt-9;

    li {
        @apply [&:not(:last-child)]:mb-3;
    }

    a {
        @apply hover:font-medium;
    }
}

.docsDecorated {
    @apply relative max-w-[684px] flex flex-wrap mobile:justify-around -mt-2 notMobile:-ml-6 pt-10 mobile:pt-4 pb-20 mobile:pb-[60px] mobile:after:bg-[url(./icons/docs-decor-mob.svg)] mobile:after:absolute mobile:after:bottom-0 mobile:after:w-[328px] mobile:after:h-[136px] mobile:after:-z-[1] z-10;

    li {
        @apply w-full max-w-[318px] mt-2 notMobile:ml-6;
    }

    a {
        @apply flex justify-between p-4 rounded-lg shadow-popUp bg-white;
    }
}

.bot {
    @apply right-0 -top-[87px] absolute pointer-events-none;
}
