    }
    89.400000% {
        transform: translate(96.771px, 111.525594px) scale(1, 1);
        animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    90.400000% {
        transform: translate(96.771px, 111.525594px) scale(1, 1.01);
        animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    94.200000% {
        transform: translate(96.771px, 111.525594px) scale(1, 0.85);
        animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    98.200000% {
        transform: translate(96.771px, 111.525594px) scale(1, 1.01);
        animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    99.200000% {
        transform: translate(96.771px, 111.525594px) scale(1, 1);
    }
    100% {
        transform: translate(96.771px, 111.525594px) scale(1, 1);
    }
}

@keyframes ear-left-move-bot-turned {
    0% {
        transform: translate(43.375011px, 75.049042px) rotate(0deg);
    }
    90.400000% {
        transform: translate(43.375011px, 75.049042px) rotate(0deg);
        animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    94.800000% {
        transform: translate(43.375011px, 75.049042px) rotate(-1deg);
    }
    99.200000% {
        transform: translate(43.375011px, 75.049042px) rotate(0deg);
    }
    100% {
        transform: translate(43.375011px, 75.049042px) rotate(0deg);
    }
}

@keyframes ear-right-move-bot-turned {
    0% {
        transform: translate(118.40938px, 57.024959px) rotate(0deg);
    }
    90.400000% {
        transform: translate(118.40938px, 57.024959px) rotate(0deg);
        animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    94.800000% {
        transform: translate(118.40938px, 57.024959px) rotate(-1deg);
    }
    99.200000% {
        transform: translate(118.40938px, 57.024959px) rotate(0deg);
    }
    100% {
        transform: translate(118.40938px, 57.024959px) rotate(0deg);
    }
}

@keyframes forhead-blink-bot-turned {
    0% {
        transform: translate(71px, 80px);
    }
    90% {
        transform: translate(71px, 80px);
        animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    94.800000% {
        transform: translate(71px, 77px);
        animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    100% {
        transform: translate(71px, 80px);
    }
}

@keyframes forhead-scale-blink-bot-turned {
    0% {
        transform: scale(1, 1);
    }
    90% {
        transform: scale(1, 1);
        animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    94.800000% {
        transform: scale(0.85, 0.66);
        animation-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    100% {
        transform: scale(1, 1);
    }
}

// Bot animations after click

@keyframes bot-turned-rotation_clicked {
    0%,
    100% {
        transform: rotate(0);
    }
    5% {
        transform: rotate(-2deg);
    }
    15% {
        transform: rotate(7deg);
    }
    50% {
        transform: rotate(-11deg);
    }
    90% {
        transform: rotate(1deg);
    }
}

