.container {
    @apply mx-auto flex justify-between max-w-layout pt-12 mobile:pt-[14px] relative;
}

.header {
    @apply relative z-[200] h-0;
}

.nav {
    @apply list-none mobile:mt-4;

    li {
        @apply text-white mr-2 mobile:mr-0 last:mr-0 inline-block text-sm font-medium mobile:text-2xl mobile:font-medium;

        > a {
            @apply font-bold py-[10px] px-6 border rounded-lg border-transparent hover:border-white;
        }

        > .activeNavLink {
            @apply bg-[#2A3266] border-[#1E282D] text-white pointer-events-none;
        }
    }

    .dropdownButton {
        @apply w-[108px] py-[10px] flex items-center font-bold justify-center border rounded-lg hover:border-[#1E282D] hover:bg-white hover:text-[#1E282D] border-transparent;
    }

    .dropdownButtonActive {
        @apply border border-[#1E282D] text-[#1E282D] bg-white border-b-0 rounded-b-none;

        .iconFill {
            @apply fill-[#1E282D] static;
        }
    }

    .dropdown {
        @apply block w-[108px] absolute top-full -mt-[1px] rounded-b-lg bg-white border-t-0 border border-[#1E282D] overflow-hidden;

        li {
            @apply w-full rounded-none p-0 mr-0 group-last:hidden;
        }

        a {
            @apply inline-block px-[10px] py-1 text-[#1E282D] text-[10px] w-full rounded-none hover:bg-[#2A3266] hover:text-white border-none;
        }
    }
}

.contacts {
    @apply w-auto;
}

.headerIsFixed {
    @apply fixed top-0 right-0 left-0 h-[76px] bg-headerRadialGradient border-b-[#1E282D] border-b;

    .container {
        @apply pt-[18px] relative;

        .link {
            @apply mobile:m-0;

            img {
                @apply mobile:w-[22px] static;
            }
        }

        > svg {
            @apply mobile:static;
        }
    }

    .nav {
        li {
            > a {
                @apply text-[#1E282D] hover:border-[#1E282D] static;
            }

            > .activeNavLink {
                @apply text-white;
            }
        }

        .dropdownButton {
            @apply text-[#1E282D] static;
        }
    }

    .iconFill {
        @apply fill-[#1E282D] static;
    }
}

@media (max-width: 1023px) {
    .responsive {
        @apply overflow-hidden -translate-x-full fixed inset-0 z-[300] before:fixed before:top-0 before:z-[-1] before:inset-0 before:bg-[#1E282D]/50 transition-all;

        &:before {
            backdrop-filter: blur(12.5px);
        }
    }

    .responsiveWrap {
        @apply flex flex-col w-[240px] h-full bg-[#1A4C6C] pt-1 pb-16;

        .nav {
            @apply ml-10 border-b border-b-white;

            li {
                > a {
                    @apply text-sm px-0 hover:border-transparent pb-2;
                }

                .activeNavLink {
                    @apply bg-transparent border-0 border-b border-b-white rounded-none;
                }

                .iconFill {
                    @apply fill-white;
                }
            }

            .dropdownButton {
                @apply w-auto border-none rounded-t-none hover:border-none bg-transparent hover:bg-transparent hover:text-white text-white text-sm py-0;
            }

            .dropdown {
                @apply flex flex-wrap flex-col relative rounded-b-none bg-transparent border-none;

                li {
                    @apply mb-0;
                }

                a {
                    @apply w-auto p-0 text-white text-[10px] hover:bg-transparent hover:text-white border-b border-b-transparent hover:border-b-white last:rounded-b-none pb-0;
                }
            }
        }

        .info {
            @apply text-white text-xs mt-10 mx-auto relative;

            svg {
                @apply hidden;
            }

            h4,
            a {
                @apply text-xs;
            }

            h4 {
                @apply mb-2;
            }

            li {
                @apply last:mt-4;
            }
        }

        .navApp {
            @apply mt-6 pl-9;

            > a {
                @apply inline-block first:mx-0;

                & + a {
                    @apply ml-4;
                }
            }
        }
    }

    .responsive_active {
        @apply translate-x-0;
    }

    .link {
        @apply mobile:-mb-[6px] mx-auto;
    }

    .logo {
        @apply w-[96px] h-[25px] block;
    }

    .nav li {
        @apply w-full text-xl text-center mb-4 mobile:text-left;
    }

    .info li:not(:last-child) {
        @apply mt-2;
    }
}

@media (max-width: 1100px) {
    .header {
        @apply px-4;
    }
}
