#support {
    .contacts {
        display: flex;
        justify-content: space-between;

        width: 100%;
        max-width: none;

        margin-top: 0;

        @media (max-width: 1023px) {
            flex-direction: column;

            margin-bottom: 0;
        }

        li {
            width: 100%;

            padding: 16px;

            border-radius: 16px;

            box-shadow: 0px 4px 25px 0px #0d95ff26;

            @media (max-width: 1023px) {
                max-width: 350px;
            }

            &:first-child {
                max-width: none;
            }

            > div {
                padding-left: 20px;
            }

            & + li {
                @media (max-width: 1023px) {
                    margin-top: 8px;
                }
            }

            h4 {
                margin-top: 4px;
                margin-bottom: 8px;

                font-size: 24px;

                @media (max-width: 1023px) {
                    font-size: 20px;
                }
            }
        }

        a {
            width: 100%;

            font-size: 18px;
            font-weight: normal;

            @media (max-width: 1023px) {
                width: 100%;

                font-size: 16px;
            }

            &:nth-child(2n) {
                padding-left: 0;
                &:before {
                    content: none;
                }
            }
        }

        svg {
            width: 40px;
            height: 40px;
            min-width: 40px;
        }
    }
}

.contacts {
    display: flex;
    margin-top: 30px;
    max-width: 494px;

    @media (max-width: 1023px) {
        display: block;
        margin: 1rem auto 1.75rem;
        max-width: none;
    }

    li {
        display: flex;

        width: 300px;

        border-radius: 8px;

        background-color: #fff;

        padding: 8px;

        color: #1e282d;

        @media (max-width: 1023px) {
            width: 320px;
        }

        &:first-child {
            max-width: 148px;

            @media (max-width: 1023px) {
                max-width: none;
            }
        }

        &:last-child {
            border-top-right-radius: 0px;

            @media (max-width: 1023px) {
                border-top-right-radius: 8px;
            }
        }

        & + li {
            margin-left: 16px;

            @media (max-width: 1023px) {
                margin-top: 16px;
                margin-left: 0;
            }
        }
    }

    svg {
        display: block;
        min-width: 24px;
        margin-top: -2px;
    }

    h4 {
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;
    }

    a {
        display: inline-block;
        width: 50%;
        position: relative;
        padding: 4px 0;
        vertical-align: top;
        font-size: 12px;
        line-height: 16px;
        font-weight: 500;

        @media (max-width: 1023px) {
            display: block;
        }

        &:nth-child(2n) {
            padding-left: 8px;

            @media (max-width: 1023px) {
                padding-left: 0;
            }

            &:before {
                content: "";
                display: inline-block;

                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;

                width: 1px;

                background-color: #0f85ff;

                @media (max-width: 1023px) {
                    content: none;
                }
            }
        }
    }
}

.contacts_unstyled {
    padding-left: 36px;

    li {
        display: flex;
        font-size: 16px;
        line-height: 1.5;
        font-weight: 500;

        & + li {
            margin-top: 40px;
        }

        h4 {
            margin-top: 2px;
            margin-bottom: 16px;

            font-weight: 600;
        }

        a {
            display: inline-block;
        }
    }
}

.contact-list {
    max-width: 348px;
    margin-top: 40px;

    @media (max-width: 1023px) {
        max-width: none;

        margin-top: 24px;
    }

    li {
        padding-bottom: 8px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        font-size: 14px;

        & + li {
            margin-top: 8px;
        }

        > div {
            display: flex;
            align-items: baseline;
            padding-left: 0;
        }

        svg {
            display: none;
        }

        h4 {
            width: 90px;
            font-weight: 600;

            @media (max-width: 1023px) {
                width: 146px;
                min-width: 146px;
            }
        }

        a {
            @media (max-width: 1023px) {
                display: block;
            }

            & + a {
                &::before {
                    content: ", ";

                    @media (max-width: 1023px) {
                        content: none;
                    }
                }
            }
        }
    }
}

footer {
    .contacts_unstyled {
        max-width: 200px;

        padding-left: 0;

        @media (max-width: 1023px) {
            margin-bottom: 16px;
        }

        li {
            > div {
                @media (max-width: 1023px) {
                    padding-left: 0;
                }
            }

            & + li {
                margin-top: 24px;
            }
        }

        h4 {
            margin-bottom: 8px;

            font-size: 14px;

            @media (max-width: 1023px) {
                font-size: 16px;
            }
        }

        a {
            font-size: 12px;

            @media (max-width: 1023px) {
                font-size: 16px;
            }
        }

        svg {
            @media (max-width: 1023px) {
                display: none;
            }
        }
    }
}
