@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&family=Ubuntu:wght@700&display=swap");

html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    font-family: "Poppins", "Arial", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #1e282d;
}

.nav-open {
    overflow: hidden;
}

.root,
#root,
#docs-root {
    --gradient: linear-gradient(333.44deg, #4114de 0%, #4cd2e9 100%);
    --sectionGradient: radial-gradient(
        145.91% 100% at 100% 100%,
        rgba(177, 225, 255, 0.5) 0%,
        rgba(177, 225, 255, 0) 100%
    );
    --headerRadialGradient: radial-gradient(
        100% 155.17% at 100% 100%,
        #b1e1ff 0%,
        #ffffff 100%
    );
    --mainHeadingGradient: radial-gradient(
        100% 155.17% at 100% 100%,
        #b1e1ff 0%,
        rgba(177, 225, 255, 0) 100%
    );
    --headerHeight: 76px;
    --headerHeightMob: 60px;
    --appAdvertIndent: 120px;
    --colorPrimary: #1e282d;
}

.nav-open {
    overflow: hidden;
    height: 100%;
    touch-action: none;
    -ms-touch-action: none;
}

/* Ham */
@media (max-width: 1023px) {
    .ham {
        width: 40px;
        height: 40px;
        position: absolute;
        right: -13px;
        bottom: -12px;
        cursor: pointer;
        -webkit-tap-highlight-color: transparent;
        transition: transform 400ms;
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
        transform: scale(-1, 1);
    }
    .hamRotate.active {
        transform: rotate(45deg);
    }
    .hamRotate180.active {
        transform: rotate(180deg);
    }
    .line {
        fill: none;
        transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
        stroke: #1e282d;
        stroke-width: 4;
        stroke-linecap: round;
    }
    .ham .top {
        stroke-dasharray: 20 160;
    }
    .ham .middle {
        stroke-dasharray: 40 142;
        transform-origin: 50%;
        transition: transform 400ms;
    }
    .ham .bottom {
        stroke-dasharray: 20 85;
        transform-origin: 50%;
        transition: transform 400ms, stroke-dashoffset 400ms;
    }
    .ham.active .top {
        stroke-dashoffset: -64px;
    }
    .ham.active .middle {
        transform: rotate(90deg);
    }
    .ham.active .bottom {
        stroke-dashoffset: -64px;
    }
}

.section-heading {
    position: relative;

    z-index: 10;
}

.section-heading h1 {
    margin: 50px 0 24px;

    font-family: Ubuntu;
    font-size: 56px;
    font-weight: 700;
    line-height: 64px;

    @media (max-width: 1023px) {
        margin: 0 0 12px;

        font-size: 24px;
        line-height: 28px;
    }
}

.section-heading h2 {
    font-size: 40px;

    @media (max-width: 1023px) {
        font-size: 24px;
    }
}

.section-heading h3 {
    font-size: 20px;
    font-weight: 500;

    @media (max-width: 1023px) {
        font-size: 16px;
    }
}

.section-heading .wide-container {
    padding-bottom: 40px;

    @media (max-width: 1023px) {
        padding-bottom: 0;
    }
}

.section-heading__decor {
    position: absolute;

    top: 0;
    right: 0;
    bottom: 0;

    width: auto;
    height: 100%;
}

.section-content .full-width {
    max-width: none;
}

.btn_outline:hover {
    background-color: #1e282d;
    color: #fff;
}

.btn_outline:hover .icon-fill {
    fill: #fff;
}

.btn_primary span {
    padding-right: 4px;
}

.btn_primary .icon-fill {
    fill: #fff;
}

.btn_primary:hover {
    background-color: #fff;
    color: #1e282d;
}

.btn_primary:hover .icon-fill {
    fill: #1e282d;
}

.btn_primary:active {
    background-color: #1e282d;
    color: #fff;
}

.btn_primary:active .icon-fill {
    fill: #fff;
}

.section.dark-theme {
    color: #fff;
}

.section.dark-theme .sub-title,
.section.dark-theme .under-title {
    color: #fff;
}

.section.dark-theme h2,
.section.dark-theme p {
    color: #fff;
}

.half > p:not(:last-child) {
    margin-bottom: 16px;
}

#home .half + .half {
    padding-left: 0;
}

.half + .half {
    padding-left: 60px;

    @media (max-width: 1023px) {
        padding-left: 0;
    }
}

h1.title {
    font-size: 56px;
    font-family: Ubuntu;
    line-height: 64px;
    letter-spacing: -0.025em;
    @media (max-width: 1023px) {
        font-size: 24px;
        line-height: 28px;
    }
}

h1 + .under-title {
    font-weight: 500;
}

h2.title {
    margin-bottom: 24px;
}
